.taro_page,
page,
.van-tabs {
  background-color: #fff;
}

page,
.taro_page {
  min-height: 100vh;
}

.doge {
  width: 5.97333rem;
  height: 3.072rem;
  margin-top: 0.17067rem;
  border-radius: 0.17067rem;
  overflow: hidden;
}

.pull-basics,
.pull-error {
  height: calc(100vh - 4.37333rem);
}

.pull-search {
  height: calc(100vh - 7.21067rem);
}

.min-pull-basics,
.min-pull-error {
  height: calc(100vh - 1.96267rem);
}

.min-pull-search {
  height: calc(100vh - 4.8rem);
}

.header {
  padding: 0.256rem;
  background: #ffffff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}
.header .left {
  -webkit-flex: auto;
          flex: auto;
}
.header .right {
  -webkit-flex: none;
          flex: none;
  padding-left: 0.256rem;
}

.placeholder {
  padding-top: 30vh;
  text-align: center;
  color: #ccc;
}
.placeholder .loadingWrapper {
  font-size: 0.512rem;
  margin-bottom: 0.512rem;
}